@charset "UTF-8";
html, body { margin: 0; }

html { font-size: 100%; }

body { background-color: #2D2D2D; font-family: Menlo, Monaco, 'Courier New', monospace; font-size: 0.9375rem; }

.nt-content { display: flex; flex-direction: column; min-height: 100vh; }

.nt-header { background-color: #2D2D2D; box-shadow: none; font-size: 90%; left: 0; position: fixed; right: 0; top: 0; transition: box-shadow 200ms ease-in-out; z-index: 1; }

.nt-scrolled .nt-header { box-shadow: 0 0 0.3125rem 0 rgba(0, 0, 0, 0.5); }

.nt-h1 { font-size: inherit; font-weight: inherit; margin: 0; }

.nt-strong { font-weight: 600; }

.nt-tabs { display: flex; }

.nt-tabs-tab { background-color: #212121; color: #FFFFFF; padding: 0.5rem 0.75rem; user-select: none; }

.nt-breadcrumb { background-color: #212121; color: #CBCECD; display: none; font-size: 90%; padding: 0.5rem 0.75rem; user-select: none; }

@media (min-width: 48rem) { .nt-breadcrumb { display: block; } }

.nt-editor { background-color: #212121; color: #CBCECD; counter-reset: line; flex: 1; padding: calc(0.625rem + 2.0625rem) 0 calc(0.625rem + 1.375rem) 0; }

@media (min-width: 48rem) { .nt-editor { padding: calc(0.625rem + 4rem) 0 calc(0.625rem + 1.375rem) 0; } }

.nt-editor-group { height: auto; overflow: hidden; transition: height 200ms ease-out; }

.nt-editor-group.nt-collapsed { height: 1.5625rem; }

.nt-editor-line { display: flex; line-height: 1.5625rem; min-height: 1.5625rem; }

.nt-editor-line:before { display: none; }

@media (min-width: 48rem) { .nt-editor-line:before { color: #909090; counter-increment: line; content: counter(line); display: block; flex: 0 0 1.875rem; padding: 0 0 0 0.625rem; text-align: right; user-select: none; } }

.nt-editor-collapse-line { align-items: center; display: flex; flex: 0 0 1.875rem; justify-content: center; text-align: center; }

.nt-editor-chevron { background-color: #212121; cursor: pointer; display: block; height: 1rem; position: relative; width: 1rem; }

.nt-editor-chevron > span:first-of-type { background-color: transparent; border-radius: 0.125rem; display: block; height: 0.125rem; left: 0; position: absolute; top: 0.375rem; transform: rotate(35deg); width: 0.625rem; }

.nt-editor-chevron > span:first-of-type:after { background-color: #FFFFFF; border-radius: 0.375rem 0.625rem 0.625rem 0.375rem; content: ''; display: block; height: 0.125rem; transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 0.8); width: 0.625rem; z-index: -1; }

.nt-editor-chevron > span:last-of-type { background-color: transparent; border-radius: 0.125rem; display: block; height: 0.125rem; left: 0.375rem; position: absolute; top: 0.375rem; transform: rotate(-35deg); width: 0.625rem; }

.nt-editor-chevron > span:last-of-type:after { background-color: white; border-radius: 0.625rem 0.375rem 0.375rem 0.625rem; content: ''; display: block; height: 0.125rem; transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 0.8); width: 0.625rem; z-index: -1; }

.nt-collapsed > .nt-editor-chevron > span:first-of-type:after { transform: rotate(-70deg); transform-origin: center center; }

.nt-collapsed > .nt-editor-chevron > span:last-of-type:after { transform: rotate(70deg); transform-origin: center center; }

.nt-editor-collapse-line-icon { opacity: 1; transition: opacity 50ms linear, visibility 0ms linear 50ms; visibility: visible; }

@media (min-width: 48rem) { .nt-editor-collapse-line-icon { opacity: 0; visibility: hidden; }
  .nt-editor-collapse-line.nt-hover .nt-editor-collapse-line-icon, .nt-collapsed > .nt-editor-collapse-line-icon { opacity: 1; transition: visibility 0ms linear, opacity 50ms linear; visibility: visible; } }

.nt-editor-line-code { display: block; flex: 1; padding: 0 0.625rem 0 calc(0.625rem * 2); text-indent: calc(-0.625rem * 2); }

.nt-editor-collapse-line.nt-active + .nt-editor-line-code { pointer-events: none; }

.nt-editor-collapse-line.nt-collapsed + .nt-editor-line-code:after { background: #2D2D2D; border-radius: 0.625rem; content: '···'; cursor: pointer; font-weight: bold; letter-spacing: -0.1875rem; margin-left: 0.3125rem; padding: 0 0.375rem 0 0.1875rem; pointer-events: all; }

.nt-editor-line-code-ml2 { margin-left: calc(0.625rem * 2); }

.nt-editor-line-code-ml4 { margin-left: calc(0.625rem * 4); }

.nt-editor-line-code-ml6 { margin-left: calc(0.625rem * 6); }

.nt-editor-line-code-ml8 { margin-left: calc(0.625rem * 8); }

.nt-editor-line-code-ml10 { margin-left: calc(0.625rem * 10); }

.nt-color-comment { color: #9a9b99; }

.nt-color-symbol { color: #c0c3c1; }

.nt-color-fn { color: #cd6700; }

.nt-color-equal { color: #606060; }

.nt-color-dec, .nt-color-obj { color: #6089b4; }

.nt-color-return, .nt-color-obj-attr { color: #9872a2; }

.nt-color-string, .nt-color-string a { color: #9aa83a; }

.nt-statusbar { background-color: #5A5A5A; bottom: 0; display: flex; font-size: 80%; justify-content: flex-end; left: 0; position: fixed; right: 0; }

.nt-statusbar-item { color: #FFFFFF; padding: 0.25rem 0.75rem; user-select: none; }

.nt-statusbar-item + .nt-statusbar-item { padding-left: 0; }
